import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Loading from '../../Loading';
import { Container, Col, Input, Button, Row } from 'reactstrap';
import { allWeekdays } from "./CalendarUtils";

const FILE_NAME = "Month.js";

class Month extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isLoaded: false
        };
    }
    componentDidMount() {
        this.setState({
            monthInfo: this.props.month,
            yearInfo: this.props.year,
            isLoaded: true
        })
    }

    render() {
        console.log(FILE_NAME + " render() : this.state :", this.state);

        return (
            <div>
                {
                    (this.state.isLoaded) ?
                        <Container>
                            <Row>
                                {
                                    allWeekdays.map(weekDay =>
                                        <Col style={{
                                            height: "50px",
                                            width: "100px",
                                            backgroundColor: "powderblue",
                                            margin: "2px"
                                        }}>
                                            <div style={{ textAlign: "center" }}>
                                                {weekDay.name}
                                            </div>
                                        </Col>
                                    )
                                }

                            </Row>
                            <Row>
                                <Col sm={5}>
                                </Col>
                                <Col sm={1}>
                                    <Button color="success">Login</Button>{' '}
                                </Col>
                                <Col sm={1}>
                                    <Button color="success">Cancel</Button>{' '}
                                </Col>
                                <Col sm={5}>
                                </Col>
                            </Row>

                        </Container> :
                        <Loading />
                }
            </div>
        );
    }

}

export default Month;