import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Loading from "../../Loading";
import { Card, CardBody, CardHeader, CardFooter } from 'reactstrap';
import { getMonthInfo } from "./CalendarUtils";
import Month from './Month';

const FILE_NAME = "Calendar.js";

class Calendar extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isLoaded: false,
            currentMonth: null
        };
    }

    componentDidMount() {
        const today = new Date();

        this.setState({
            currentMonth: getMonthInfo(today.getMonth(), today.getFullYear()),
            isLoaded: true
        });
    }

    render() {
        console.log(FILE_NAME + " render() : this.state :", this.state);

        return (
            (this.state.isLoaded) ?
                <Card>
                    <Month month={6} year={2020} />
                </Card> : <Loading />
        );
    }

}

export default Calendar;